@charset "UTF-8";
body {
  font-family: "PingFang SC", "Lantinghei SC", "Open Sans", Arial, "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", STHeiti, "WenQuanYi Micro Hei", SimSun, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  word-wrap: break-word;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased; }

.container {
  width: auto;
  max-width: 1020px !important;
  padding-left: 20px;
  padding-right: 20px; }

a,
#site-header a {
  color: #2196f3; }

#site-header a:hover,
#site-header a:active {
  text-decoration: none;
  color: #f4645f;
  cursor: pointer; }

a {
  color: #2196f3; }
  a:hover, a:active {
    text-decoration: none;
    color: #f4645f;
    cursor: pointer; }

.navbar {
  margin-bottom: 0; }
  .navbar .navbar-brand,
  .navbar .fa-search {
    color: #f4645f !important; }
  .navbar input.form-control {
    font-size: 0.8em;
    padding: 6px 16px; }

.share {
  margin: 20px 0; }

.panel {
  box-shadow: none; }

.panel-primary > .panel-heading {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #212121;
  border-bottom: 2px solid #2196f3; }

.pagination {
  padding: 20px 0; }

.pagination a.active {
  background: #2196f3;
  border-color: #2196f3;
  z-index: 2;
  color: #ffffff;
  cursor: default; }

#tag-cloud a {
  border: 1px solid #eee;
  padding: 3px 7px;
  color: #2196f3;
  font-size: 1em;
  line-height: 1.3em;
  display: inline-block;
  margin: 10px 5px;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease; }

#tag-cloud {
  padding: 1px 10px; }
  #tag-cloud a:hover {
    color: #ffffff !important;
    background: #f4645f;
    border: 1px solid #f4645f;
    text-decoration: none; }

#archive-list a:hover {
  color: #2196f3; }

#archive-list .list-group-item {
  border: 1px solid #eee;
  border-radius: 3em;
  height: 2.4em;
  line-height: 2.4em;
  padding: 0 16px;
  margin-top: 0.6em;
  transition: all 0.2s; }
  #archive-list .list-group-item:hover {
    border-color: #f4645f; }

#archive-list .badge {
  margin-top: 0.75em; }

#site-footer {
  position: relative;
  margin-top: 40px;
  padding: 40px 0;
  font-size: 0.8em;
  line-height: 1.5;
  border-top: 1px solid #eee; }
  #site-footer .fa-arrow-circle-up {
    color: #f4645f; }
  #site-footer .fa-github {
    position: absolute;
    top: 35px;
    left: 50%;
    height: 34px;
    width: 34px;
    margin-left: -17px;
    font-size: 34px;
    color: #ccc; }
    #site-footer .fa-github:hover {
      color: #888; }

#friend-links li {
  float: left;
  height: 2em;
  line-height: 2em;
  font-size: 1em;
  padding: 0.3em 0.8em;
  list-style: none; }

#hot-article-list li {
  font-size: 0.9em;
  padding: 10px 15px; }

.markdown-body pre > code {
  padding: 0;
  margin: 0;
  font-size: 100%;
  word-break: normal;
  white-space: pre;
  background: 0 0;
  border: 0; }

.markdown-body pre {
  padding: 16px;
  overflow: auto;
  font-size: 85%;
  line-height: 1.45;
  background-color: #f7f7f7;
  border-radius: 3px;
  border: 0; }

.markdown-body pre code {
  background-color: transparent;
  border: 0; }

.article-list {
  position: relative;
  padding-left: 0; }
  .article-list .article-list-item {
    position: relative;
    padding-top: 30px;
    padding-bottom: 15px;
    list-style: none;
    border-bottom: 1px solid #eee; }
  .article-list .article-list-name {
    font-weight: 400;
    margin: 0 0 8px;
    font-size: 20px;
    line-height: 1.2; }
    .article-list .article-list-name:hover {
      text-decoration: none; }
  .article-list .article-list-description {
    max-width: 550px;
    margin-top: 8px;
    margin-bottom: 0;
    font-size: 14px;
    color: #888; }
  .article-list .article-list-meta {
    display: block;
    margin-top: 8px;
    margin-bottom: 0;
    font-size: 13px;
    color: #888; }
    .article-list .article-list-meta span {
      margin-right: 0.6em; }

.mobile-visible {
  display: none; }

.jumbotron {
  padding-top: 28px;
  padding-bottom: 28px;
  background-color: #2196f3; }
  .jumbotron .jumbotron-title, .jumbotron .jumbotron-desc, .jumbotron .jumbotron-meta {
    color: #ffffff; }
  .jumbotron .jumbotron-title {
    font-size: 2em; }
  .jumbotron .jumbotron-desc {
    font-size: 1.2em; }
  .jumbotron .jumbotron-meta {
    font-size: 1em; }
    .jumbotron .jumbotron-meta a {
      color: #ffffff; }
      .jumbotron .jumbotron-meta a:hover {
        color: #f4645f; }

.home-jumbotron .jumbotron-title {
  font-size: 2.8em; }

@media (max-width: 50em) {
  .mobile-block {
    display: block;
    float: none; }
  .mobile-hidden {
    display: none; }
  .mobile-visible {
    display: inline-block; }
  .markdown-body h1 {
    font-size: 2em !important; }
  .markdown-body h2 {
    font-size: 1.5em !important; }
  .markdown-body h3 {
    font-size: 1.3em !important; }
  .markdown-body h4 {
    font-size: 1.2em !important; }
  .markdown-body h5 {
    font-size: 1em !important; }
  .jumbotron .jumbotron-title {
    font-size: 1.8em !important; }
  .jumbotron .jumbotron-desc {
    font-size: 1.2em !important; }
  .jumbotron .jumbotron-meta {
    font-size: 0.9em !important; }
  .home-jumbotron .jumbotron-title {
    font-size: 2.4em !important; } }

@media (max-width: 35em) {
  .jumbotron {
    padding-top: 20px;
    padding-bottom: 20px; }
  .jumbotron .jumbotron-title {
    font-size: 1.6em !important; }
  .jumbotron .jumbotron-desc {
    font-size: 1em !important; }
  .home-jumbotron .jumbotron-title {
    font-size: 2.1em !important; } }
